@tailwind base;
@tailwind components;
@tailwind utilities;

/* --- Google Translate Widget Fixes --- */

/* Hides the Google Translate banner at the top */
.goog-te-banner-frame.skiptranslate {
    display: none !important;
} 

/* Prevents the page content from being pushed down by the banner */
body { 
    top: 0px !important; 
}

/* Overrides the default widget styles to make it fit better in your header */
#google_translate_element .goog-te-gadget-simple {
  background-color: transparent !important;
  border: none !important;
  cursor: pointer;
}

#google_translate_element .goog-te-gadget-simple .goog-te-menu-value span {
  color: #374151; /* Corresponds to Tailwind's text-gray-700 */
  font-size: 0.875rem; /* Corresponds to Tailwind's text-sm */
}

/* Style for dark mode */
.dark #google_translate_element .goog-te-gadget-simple .goog-te-menu-value span {
    color: #D1D5DB; /* Corresponds to Tailwind's dark:text-gray-300 */
}

/* Hides the "Powered by" text and logo */
#google_translate_element .goog-logo-link {
    display: none !important;
}